<template>

    <div class="container">

     
	<div class="row justify-content-center">



<div class=" mx-auto col-md-3 order-md-2">
  <br>
    <h3></h3>
  <p></p>
  <br>
<div class="card" >
  <div class="card-body">
    <h4 class="card-title">()</h4>
    <img src="../assets/xiaohuihuiwx.jpg" alt="" class="card-img">
    <p class="card-text"></p>
  </div>
</div>
</div>


<div class="col-md-9 order-md-1  text-md-left pr-md-5">  <br>

<form @submit.prevent="submit">
  <h3></h3><br>
  <div class="form-group">
    <label for="exampleInputEmail1"></label>
    <input  v-model="url" type="url" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="">
    <small id="emailHelp" class="form-text text-muted">.</small>
  </div>
  <button type="submit" class="btn btn-primary"></button>
   <br> <br>
</form>

</div>


</div>
</div>
</template>

<script>
import news from '../api/news.js';

  export default {
    name: 'app',
    data(){
        return{
           url:""
        }
    },
    methods: {
       submit: function(event) {
            
              news.post(this.url,function(err,data){
                if(data == "0"){
                  alert("")
                }else{
                  alert("")
                }
              });

           
        }
        
    },
    components: {
      
    }
  }
</script>



