<template>
<div>
    <div class="weui-cells__title"> > </div>


    <div class="weui-cells__title"></div>

    <div class="weui-cells">
        <div class="weui-cell">
            <div class="weui-cell__bd">
                <input class="weui-input" type="text" placeholder="">
            </div>
        </div>
    </div>

    <div class="weui-cells__title"></div>

    <div class="weui-cells">
        <div class="weui-cell">
            <div class="weui-cell__bd">
                <input class="weui-input" type="text" placeholder="https://mp.weixin.qq.com/s...">
            </div>
        </div>
    </div>

    <div class="weui-cells__title"></div>
    <weui-textarea placeholder="" :value="text"></weui-textarea>

    <div class="weui-cells__title"></div>
    <div class="weui-cells">
        <div class="weui-cell weui-cell_select weui-cell_select-before">
            <div class="weui-cell__hd">
                <select class="weui-select" name="select2">
                    <option value="1"></option>
                    <option value="2"></option>
                </select>
            </div>
            <div class="weui-cell__bd">
                <input class="weui-input" type="number" pattern="[0-9]*" placeholder="">
            </div>
        </div>
    </div>
   
    <div class="weui-btn-area">
        <a class="weui-btn weui-btn_primary" href="javascript:" id="showTooltips"></a>
    </div>
</div>
</template>

<script>
import news from '../../api/news.js';
import WeuiTextarea from '@/components/weui/Textarea';

  export default {
    name: 'app',
    data(){
        return{
           url:"",
           text:""
        }
    },
    methods: {
       submit: function(event) {
            
              news.post(this.url,function(err,data){
                if(data == "0"){
                  alert("")
                }else{
                  alert("")
                }
              });

           
        }
        
    },
    components: {
      'weui-textarea':WeuiTextarea
    }
  }
</script>



